<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <div class="charts">
          <div v-if="mapParams.options.JSON!==cookieCity" class="map-back" @click="mapBack"></div>
          <transition name="fade">
            <Map v-if="mapFlag" :params="mapParams" @change="handleMap"></Map>
          </transition>
        </div>
      </div>
      <div class="w-right">
        <div class="part ct-top">
          <yjzt v-if="yjztData.length" :data="yjztData"></yjzt>
        </div>
        <div class="part ct-bottom">
          <ctitle
            :title="`${shej_02}${shij_02==='invalid'?'':shij_02}${xj_02==='invalid'?'':xj_02}信访量走势`"></ctitle>
          <div class="chart">
            <LinesR :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Cookies from 'js-cookie'
  import {get_http} from "@/api/index";
  import timeChange from "@/components/timeChange/index";
  import ctitle from "@/components/title/index";
  import LinesR from "@/components/line/LinesR";
  import Map from "@/components/map/Map2";
  import yjzt from "@/components/yjzt/index";

  export default {
    name: "xstz",
    components: {
      ctitle,
      LinesR,
      timeChange,
      Map,
      yjzt
    },
    data() {
      return {
        curCity: "广东省",
        shijCity: "",
        shej_02: "invalid",
        shij_02: "invalid",
        xj_02: "invalid",
        nameP: "",
        mapJson: {
          "广东省": "440000",
          "广州市": "440100",
          "韶关市": "440200",
          "深圳市": "440300",
          "珠海市": "440400",
          "汕头市": "440500",
          "佛山市": "440600",
          "江门市": "440700",
          "湛江市": "440800",
          "茂名市": "440900",
          "肇庆市": "441200",
          "惠州市": "441300",
          "梅州市": "441400",
          "汕尾市": "441500",
          "河源市": "441600",
          "阳江市": "441700",
          "清远市": "441800",
          "东莞市": "441900",
          "中山市": "442000",
          "潮州市": "445100",
          "揭阳市": "445200",
          "云浮市": "445300",
        },
        //1.地图
        mapFlag: false,
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            isHome: false,
            JSON: "440000",
            xName: "name",
            yName: "value",
            map: {
              "value": "信访量"
            },
            carousel: false
          }
        },
        //2.预警状态
        yjztData:[],
        // 3.受理量走势
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "rgba(220, 150, 209, 1)",
              start: "rgba(220, 150, 209, 0.3)",
              end: "rgba(220, 150, 209, 0)"
            },
          ],
          option: {
            showSymbol:true,
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "日",
            axisYName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "10%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
        },
      };
    },
    mounted() {
      for (let key in this.mapJson) {
        if (this.mapJson[key] === this.cookieCity) {
          this.curCity = key
        }
      }
      this.mapParams.options.JSON = this.cookieCity;
      this.getMapAPI();
    },
    methods: {
      // 1.全省信访量-地图
      getMapAPI() {
        // 处理接口参数
        // 省级
        if (this.curCity === '广东省') {
          this.shej_02 = this.curCity;
          this.shij_02 = 'invalid';
          this.xj_02 = 'invalid';
          this.nameP = 'shij_02';
        } else {
          // 市级
          if (this.shijCity) {
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = 'invalid';
            this.nameP = 'xj_02';
          } else {
            // 县区级
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = this.curCity;
            this.nameP = 'invalid';
          }
        }

        get_http("xfzlyjZlyjMap", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
        })
          .then(res => {

            this.mapParams.data = res.data.data;
            this.mapFlag = true;
            this.getAPI();
          })
          .catch(err => {
            console.log(err);
          });
      },
      getAPI() {
        console.log("shej_02:" + this.shej_02);
        console.log("shij_02:" + this.shij_02);
        console.log("xj_02:" + this.xj_02);
        this.getYjztAPI();
        this.getZsLineAPI();
      },
      // 2.预警状态
      getYjztAPI() {
        get_http("xfzlyjZlyjYjzt", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
        })
          .then(res => {
            this.yjztData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 3.受理量走势
      getZsLineAPI() {
        get_http("xfzlyjZlyjzsLine", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
        })
          .then(res => {
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击地图下钻
      handleMap(name) {
        this.curCity = name;
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            this.mapFlag = false;
            this.mapParams.options.JSON = this.mapJson[name];
          }
        }
      },
      // 地图返回全省
      mapBack() {
        this.mapFlag = false;
        this.curCity = "广东省";
        this.mapParams.options.JSON = "440000";
      },
    },
    computed: {
      cookieCity(){
        return Cookies.get('xzqhdm')
      }
    },
    watch: {
      curCity: function (newVal, oldVal) {
        if (newVal === '广东省') {
          this.getMapAPI();
        } else {
          let c = '';
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.getMapAPI();
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            this.getAPI()
          }
        }
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box
      .chart
        box-sizing border-box
        width 100%
        height calc(100% - 0.28rem)
      .w-left
        position relative
        flex 1
        display flex
        flex-direction column
        .charts
          position relative
          flex 1
          width 100%
          height 100%
        .fade-enter-active, .fade-leave-active
          transform scale(1)
          transition all 0.8s

        .fade-enter, .fade-leave-to
          opacity 0
          transform scale(0)
        .map-back
          position absolute
          top 30px
          right 60px
          z-index 1000
          width 30px
          height 30px
          background url("~@/assets/back-icon.png") no-repeat
          cursor pointer
      .w-right
        display flex
        flex 1
        flex-direction column
        box-sizing border-box
        .part
          position relative
          flex 1
          width 100%
          height 100%
        .ct-top
          .chart
            position relative
        .ct-bottom
          width 100%
</style>
